@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.gameButton{
    background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

.gameButton:hover {
    background: #c23866;
  color: #fff;
}

.Game {
    font-family: Raleway, sans-serif;
    margin: 0;
    font-size: 1.5em;
    text-align: center;
    background: #1b1523;
    color: #fff;
  }

.card-grid{
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

.card {
  position: relative;
}

.card {
  position: relative;
}
.card img {
  width: 100%;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}

/* front of card - the picture */
.card .front {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/* back of card - cover */
.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}
* {
    font-family: 'Quicksand';
}

.wrapper {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
.container {
    height: 90vh;
    background-image: url("https://source.unsplash.com/600x900/?nature,water"); 
    background-repeat: no-repeat;
    background-size:cover;
    opacity: 0.8;
}

.row {
    height: 80vh;
}

  .main {
    height: 90vh;
    /* background: #fff;*/
    /* background-image: url("https://source.unsplash.com/600x900/?nature,water");  */
    box-shadow: 0px 13px 40px -13px hsla(0, 1%, 42%, 0.878);
    width: auto;
    max-width: 80%;
    margin: 0 auto;
  }
  
  .title-container {
    height: 90vh;
    /* background: url("img/1.jpg") center center no-repeat; */
    background-size: fill;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: hsla(0, 1%, 42%, 0.878);
  }
  
  .title-container__title {
    font-size: 50px;
    letter-spacing: 2px;
    line-height: 1.3;
  }
  
  .title-container__subtitle {
    font-style: italic;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.5;
  }
  
  .form-container {
    /* background-color: white; */
    height: 100%;
    padding-top: 100px;
    padding-left: 50px;
  }
  
  .inputi {
    /* background-color: white; */
    border: 0;
    border-bottom: solid 1px hsl(303, 85%, 63%);
    width: 30%;
    padding-bottom: 4px;
    color: black !important;
    font-weight: lighter;
    letter-spacing: 2px;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
  }
  
  .inputi:-internal-autofill-selected {
    background-color: none;
  }

  .inputi {
      outline: none;
  }
  
  .inputi:-webkit-autofill {
      /* -webkit-box-shadow: 0 0 0 30px white inset; */
      -webkit-text-fill-color: black !important;
  }
  
  .button-weather {
    border: 0;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    font-weight: lighter;
    letter-spacing: 1px;
    font-size: 15px;
    cursor: pointer;
    background-color: hsl(303, 85%, 63%);
    color: #fff;
    font-weight: 100;
  }
  
  .button-weather:active {
    outline: none;
  }
  
  .weather__info {
    width: 60%;
    font-size: 20px;
    font-weight: 200;
    letter-spacing: 2px;
  }
  
  .weather__key {
    color: white;
    border-bottom: solid 2px hsla(0, 1%, 42%, 0.878);
    padding: 20px 0 20px 0;
    font-weight: 400;
  }
  
  .weather__key:last-child {
    border: 0;
  }
  
  .weather__value {
    color: white;
    font-weight: 200;
  }
  
  .weather__error {
    color: white;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 200;
  }
